import React from 'react';
import $ from 'jquery';
import './App.css';


const API = `https://jsonplaceholder.typicode.com/users`;


export default class App extends React.Component {

  render() {
    return (
      <div className="Header">
        <Header />
        <Content />
      </div>
    )
  }
}

class Header extends React.Component {
  render() {
    return (
      <>
        <h1>Users</h1>
      </>
    )
  }
}

class Content extends React.Component {
  constructor(state) {
    super(state);
    this.state = {
      data: [],
      showUsers: false
    }
  }
  componentDidMount = () => {
    $.ajax({
      url: API,
      type: 'GET',
      dataType: 'json',
      contentType: 'application/json',
      success: function(data){
        this.setState({data});
      }.bind(this),
      error: function(jqXHR){
        console.log(jqXHR);
      }
    });
  }

  render() {
    return (
      <>
        <button onClick={() => this.setState({showUsers: !this.state.showUsers})} className="button">Показать</button>
        { this.state.showUsers ? 
          <table>
            <tbody>{this.state.data.map((item, key) => {
              return (
                <tr key={key} className="opacity">
                  <td>{item.name}</td>
                  <td>{item.email}</td>
                  <td>{item.address.street}</td>
                </tr>
              )})}
            </tbody>
          </table>
          : null
        }
      </>
    )
  }
}